import React, { useEffect, useRef, useState, useMemo } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { useWeb3React } from '@web3-react/core/core/core';
import { useTranslation } from 'contexts/Localization';
import { ButtonMenu, ButtonMenuItem, Button, Flex, Text, Box} from '@pancakeswap/uikit';
import ConnectWalletButton from 'components/ConnectWalletButton';
import { createClient } from '@supabase/supabase-js';
import EmojiPicker from './EmojiPicker';
import ReactionPicker from './ReactionPicker';
import { ChatBox, Input, Container, Title,Title2 , Box2} from './StyledComponents';
import styles from './ChatAppStyles';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_KEY;
const supabase = createClient(supabaseUrl, supabaseKey);

const Profile: React.FC = () => {
  const { account } = useWeb3React()
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState(''); 
  const [cooldown, setCooldown] = useState(0);
  const chatBoxRef = useRef(null);
  const location = useLocation();
  const [topList, setTopList] = useState([]);
  const [language, setLanguage] = useState('hu'); // AlapÃ©rtelmezett nyelv: magyar
  const handleLanguageChange = (lang) => {
    setLanguage(lang);
  };
  const [currentPage, setCurrentPage] = useState(1);
  const [names, setNames] = useState({});
  const [userNumbers, setUserNumbers] = useState({});
  const [isSearching, setIsSearching] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [emojiPickerVisible, setEmojiPickerVisible] = useState(false);
  const [image, setImage] = useState(null);
  const [selectedMessageId, setSelectedMessageId] = useState(null); // KivÃ¡lasztott Ã¼zenet ID
  const [enlargedImage, setEnlargedImage] = useState(null);  // KÃ©p nagyÃ­tÃ¡s Ã¡llapota
  const [languageBoxVisible, setLanguageBoxVisible] = useState(false);
  const itemsPerPage = 25;
  const { t } = useTranslation();
  const [displayedMessages, setDisplayedMessages] = useState(20);
  const fileInputRef = useRef(null);

  const handleImageClick = (src) => {
    setEnlargedImage(src);  // KÃ©p forrÃ¡sÃ¡nak tÃ¡rolÃ¡sa a modalhoz
  };
  
  const handleModalClose = () => {
    setEnlargedImage(null);  // Modal bezÃ¡rÃ¡sa
  };
  
  const handleSendImage = (imageSrc) => {
    setImage(imageSrc); // Set the selected image for sending
  };

  const handleScroll = async (e) => {
    const chatBox = chatBoxRef.current;
  
    // Ha mÃ¡r betÃ¶ltÃ¶ttÃ¼k a 100 Ã¼zenetet, ne tÃ¶ltsÃ¶n be tÃ¶bbet
    if (displayedMessages >= 100) {
      return;
    }
  
    // Check if the user has scrolled to the top
    if (chatBox.scrollTop === 0 && displayedMessages < Math.min(100, messages.length)) {
      const currentScrollHeight = chatBox.scrollHeight;
      const oldestTimestamp = messages[0]?.timestamp;
  
      const { data, error } = await supabase
        .from('messages')
        .select('*')
        .order('timestamp', { ascending: false })
        .lt('timestamp', oldestTimestamp)
        .limit(20); // Load 20 more messages
  
      if (error) {
        console.error('Error fetching older messages:', error);
      } else {
        setMessages((prevMessages) => [...data.reverse(), ...prevMessages].slice(-100)); // Keep only the latest 60 messages
        setDisplayedMessages((prev) => Math.min(prev + 20, Math.min(100, messages.length)));
  
        setTimeout(() => {
          const newScrollHeight = chatBox.scrollHeight;
          chatBox.scrollTop = newScrollHeight - currentScrollHeight;
        }, 0);
      }
    }
  };  
   
  
  const handleDeleteMessage = async (msg) => {
    if (!window.confirm('Are you sure you want to delete this message?')) {
      return;
    }
  
    try {
      const { error } = await supabase
        .from('messages')
        .delete()
        .eq('id', msg.id) // FeltÃ©telezve, hogy az Ã¼zenetnek van egyedi azonosÃ­tÃ³ja
        .eq('sender', account); // Csak a sajÃ¡t Ã¼zenetedet tudod tÃ¶rÃ¶lni
  
      if (error) {
        console.error('Error deleting message:', error.message);
        return;
      }
  
      // TÃ¶rÃ¶ljÃ¼k az Ã¼zenetet a helyi Ã¡llapotbÃ³l is
      setMessages(prevMessages => prevMessages.filter(m => m.id !== msg.id));
    } catch (error) {
      console.error('Unexpected error deleting message:', error);
    }
  };
  const toggleReactionsVisibility = (messageId) => {
    // Toggle the message ID; if the same message is clicked, it will hide reactions
    setSelectedMessageId((prevId) => (prevId === messageId ? null : messageId));
  };

  useEffect(() => {
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  }, [account, messages,language]); 

  useEffect(() => {
    const fetchMessages = async () => {
      const { data, error } = await supabase
        .from('messages')
        .select('*, reactions')
        .order('timestamp', { ascending: false })
        .limit(100); // Limit the initial fetch to 100 messages
    
      if (error) {
        console.error('Error fetching messages:', error);
      } else {
        setMessages(data.reverse().slice(-100));  // Limit to 100 messages
      }
    };
  
    fetchMessages();
  }, []);
  
  
  useEffect(() => {
    const channel = supabase
      .channel('public:messages')
      .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'messages' }, payload => {
        const newMessage = payload.new;
        setMessages(prevMessages => {
          const updatedMessages = [...prevMessages, newMessage];
          return updatedMessages;
        });
      })
      .subscribe();
    
    return () => {
      supabase.removeChannel(channel);
    };
  }, []);
  
  const userList = useMemo(() => {
    const usersFromMessages = messages.map(msg => msg.sender);
    const usersFromTopList = topList.map(([user]) => user);
    return Array.from(new Set([...usersFromMessages, ...usersFromTopList])); // Egyedi felhasznÃ¡lÃ³k Ã¶sszegyÅ±jtÃ©se
  }, [messages, topList]);

  useEffect(() => {
    if (!account || userList.length === 0) return;
    
    const fetchUserData = async () => {
      const newNames = {};
      const newNumbers = {};
    
      await Promise.all(userList.map(async (user) => {
        try {
          const { data: existingData, error: fetchError } = await supabase
            .from('user_data')
            .select('*')
            .eq('account', user)
            .single();
    
          if (fetchError && fetchError.code !== 'PGRST116') {
            console.error(`Error fetching data from Supabase for ${user}:`, fetchError);
            return;
          }
    
          if (existingData) {
            newNames[user] = existingData.name;
            newNumbers[user] = existingData.number;
          }
        } catch (error) {
          console.error(`Error fetching data for ${user}:`, error);
        }
      }));
    
      setNames(newNames);
      setUserNumbers(newNumbers);
    };
    
    fetchUserData();
  }, [account, userList]);
    
  useEffect(() => {
    if (cooldown > 0) {
      const timer = setTimeout(() => setCooldown(cooldown - 1), 1000);
      return () => clearTimeout(timer);
    }
    return undefined; // HozzÃ¡adunk egy visszatÃ©rÃ©si Ã©rtÃ©ket akkor is, ha cooldown <= 0
  }, [cooldown]);
  
  const handleSearchInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  
  const handleImageChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
    
      if (file.size > 1048576) {
        alert('The file size must be less than 1 MB.');
        return;
      }
    
      const reader = new FileReader();
      reader.onload = (e) => {
        setImage(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAddReaction = async (messageId, emoji) => {
    const { data, error } = await supabase
      .from('messages')
      .select('reactions')
      .eq('id', messageId)
      .single();
  
    if (error) {
      console.error('Error fetching reactions:', error);
      return;
    }
  
    const reactions = data.reactions || {};
  
    // EllenÅ‘rizzÃ¼k, hogy a felhasznÃ¡lÃ³ ugyanazt az emojit vÃ¡lasztja-e (ez esetben tÃ¶rÃ¶ljÃ¼k a reakciÃ³t)
    if (reactions[account] === emoji) {
      delete reactions[account]; // ReakciÃ³ tÃ¶rlÃ©se
    } else {
      reactions[account] = emoji; // ReakciÃ³ hozzÃ¡adÃ¡sa vagy frissÃ­tÃ©se
    }
  
    const { error: updateError } = await supabase
      .from('messages')
      .update({ reactions })
      .eq('id', messageId);
  
    if (updateError) {
      console.error('Error updating reactions:', updateError);
    } else {
      // Helyi Ã¡llapot frissÃ­tÃ©se az Ãºj reakciÃ³kkal
      setMessages(prevMessages =>
        prevMessages.map(msg =>
          msg.id === messageId ? { ...msg, reactions } : msg
        )
      );
    }
  };
  
  
  const handleSend = async () => {
    if (cooldown > 0 || !account) return;  // Ensure there is a logged-in user
  
    // If there's no text but there is an image, add a zero-width space
    const messageText = newMessage.trim() || (image ? '\u200B' : '');
  
    if (!messageText && !image) return;  // Ensure that we have either text or an image
   
    const message = {
      text: messageText,  // Use the modified messageText
      sender: account,
      image,
      language, // HozzÃ¡adjuk a nyelvet az Ã¼zenethez
    };
  
    const { data, error } = await supabase
      .from('messages')
      .insert([message]);
  
    if (error) {
      console.error('Error sending message:', error);
      alert(`Failed to send message: ${error.message}`);
    } else {
      setNewMessage('');
      setImage(null);
      setCooldown(20);
    }
  };

  const getSenderName = (sender) => {
    if (!sender) {
      return 'Unknown';
    }
    
    if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
      return 'Admin';
    }
    
    return names[sender] || `${sender.substring(0, 5)}...${sender.substring(sender.length - 5)}`;
  };
  
  const getSenderStyle = (sender) => {
    // Admin Ã©s egyÃ©b meghatÃ¡rozott cÃ­mek szÃ­nei
    if (
      sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1' || // Admin cÃ­m
      sender === '0xde6c77ac73661e5df07b6ed464d21c41d3d6d94b'      // Ãšj arany cÃ­m
    ) {
      return { color: 'gold' };
    }
  
    if (sender === '0x024887Ddd168caDf24e582F85bf952b0dF47E815') {
      return { color: '#4169e1' }; // KirÃ¡lykÃ©k szÃ­n
    }
  
    if (sender === '0x533E4503572A01FEED9ABa72761555D7f62998c3') {
      return { color: '#BFFF00' }; // Uranium szÃ­n (fluoreszkÃ¡lÃ³ zÃ¶ld)
    }
  
    // Minden mÃ¡s felhasznÃ¡lÃ³nak ezÃ¼st szÃ­n
    return { color: '#E5E4E2' };
  };
  

  const getSenderImage = (sender) => {
    const userNumber = userNumbers[sender];
    
    if (userNumber) {
      let formattedNumber;
  
      if (userNumber < 10) {
        // KÃ©tjegyÅ±re formÃ¡zzuk, ha a szÃ¡m kisebb, mint 10
        formattedNumber = userNumber.toString().padStart(2, '0');
      } else {
        // HÃ¡romjegyÅ±re formÃ¡zzuk, ha a szÃ¡m 10 vagy nagyobb
        formattedNumber = userNumber.toString().padStart(3, '0');
      }
  
      return `images/pixel/${formattedNumber}.jpg`;
    }
  
    return 'images/pixel/200.png';  // AlapÃ©rtelmezett kÃ©p, ha nincs szÃ¡m
  };
  

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp); // Eredeti UTC idÅ‘
    const timeString = date.toLocaleTimeString(navigator.language, {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false,
    });
    const dateString = date.toLocaleDateString(navigator.language, {
      month: 'long',     // HÃ³nap neve
      day: 'numeric',    // Nap
    });
    return `${timeString}, ${dateString}`;
  };
  
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentItems = topList.slice(startIndex, endIndex);

  const totalPages = Math.ceil(topList.length / itemsPerPage);
  

  const filteredMessages = messages.filter((msg) => msg.language === language);

  return (
    <>
   {account ? (
    <Container>
     
                    <Flex
      alignItems="center"
      justifyContent="space-between"
      style={{
        padding: '10px',
        backgroundColor: '#000',
        borderRadius: '10px', // Border radius hozzÃ¡adÃ¡sa
      }}
    >
      {/* VizslaChat cÃ­m kÃ¶zÃ©pre igazÃ­tva a bal oldalon */}
      <Title style={{ fontSize: '28px', fontWeight: 'bold', color: '#fff', textAlign: 'center' }}>
        VizslaChat
      </Title>

      {/* NyelvvÃ¡lasztÃ³ Ã©s privÃ¡t chat/setting gombok egy sorban */}
      <Flex alignItems="center">
        {/* NyelvvÃ¡lasztÃ³ nyithatÃ³ box, csak zÃ¡szlÃ³kkal Ã©s lefelÃ© mutatÃ³ nyÃ­l */}
        <div style={{ position: 'relative', marginRight: '10px' }}>
          <Button
            onClick={() => setLanguageBoxVisible(!languageBoxVisible)}
            style={{
              backgroundColor: 'transparent',
              color: '#fff',
              border: '1px solid #fff',
              padding: '5px 10px',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <img
              src={language === 'hu' ? 'images/pixel/777.png' : 'images/pixel/776.png'}
              alt={language === 'hu' ? 'Hungarian Flag' : 'English Flag'}
              style={{ width: '20px' }}
            />
            {/* LefelÃ© mutatÃ³ nyÃ­l */}
            <span style={{ marginLeft: '8px' }}>{languageBoxVisible ? 'â–²' : 'â–¼'}</span>
          </Button>

          {languageBoxVisible && (
            <div
              style={{
                position: 'absolute',
                top: '100%',
                right: 0,
                backgroundColor: '#000', // Fekete belsÅ‘
                padding: '10px',
                border: '1px solid #ddd',
                borderRadius: '5px',
                zIndex: 1000,
              }}
            >
              {/* Csak a mÃ¡sik nyelv jelenik meg */}
              {language === 'hu' ? (
                <div
                  style={{ cursor: 'pointer', color: '#fff' }}
                  onClick={() => handleLanguageChange('en')}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleLanguageChange('en');
                    }
                  }}
                  role="button"
                  tabIndex={0}
                >
                  <img
                    src="images/pixel/776.png"
                    alt="English Flag"
                    style={{ width: '20px', marginRight: '5px' }}
                  />
                </div>
              ) : (
                <div
                  style={{ cursor: 'pointer', color: '#fff' }}
                  onClick={() => handleLanguageChange('hu')}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleLanguageChange('hu');
                    }
                  }}
                  role="button"
                  tabIndex={0}
                >
                  <img
                    src="images/pixel/777.png"
                    alt="Hungarian Flag"
                    style={{ width: '20px', marginRight: '5px' }}
                  />
                </div>
              )}
            </div>
          )}
        </div>

        {/* Private Chat gomb (Ã¡tnevezve PrivateChat-re) */}
        <Link to="/privatechat" style={{ textDecoration: 'none' }}>
          <Button
            style={{ 
              backgroundColor: 'transparent',
              color: '#fff',
              border: '1px solid gold', 
              padding: '5px 10px',
              marginRight: '5px',
              cursor: 'pointer',
            }}
          >
            PrivateChat
          </Button>
        </Link>

        {/* Settings felirat (fogaskerÃ©k helyett) */}
        <Link to="/settings" style={{ textDecoration: 'none' }}>
          <Button
            style={{
              backgroundColor: 'transparent',
              color: 'gold',
              border: 'none',
              padding: '5px',
              cursor: 'pointer',
            }}
          >
            âš™ï¸
          </Button>
        </Link>
      </Flex>
    </Flex>

        <ChatBox ref={chatBoxRef} onScroll={handleScroll}>
  {filteredMessages.slice(-displayedMessages).map((msg) => (
    
    <div
    key={msg.id}
    style={{
      ...styles.messageContainer,
      backgroundColor: selectedMessageId === msg.id ? '#000000' : '#111111', // Black on selection, dark gray otherwise
      borderRadius: '20px',
      padding: '10px',
      marginBottom: '10px'
    }}
    onClick={() => toggleReactionsVisibility(msg.id)}
    onKeyDown={(e) => {
      if (e.key === 'Enter' || e.key === ' ') {
        toggleReactionsVisibility(msg.id);
      }
    }}
    role="button"
    tabIndex={0}
  >
      {/* FeladÃ³ kÃ©pe */}
      <img src={getSenderImage(msg.sender)} alt="sender" style={styles.avatar} />
      {/* Ãœzenet szÃ¶vege Ã©s szemetes ikon flexbox-szal elrendezve */}
      <div style={{ ...styles.message, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        {/* Bal oldalon az Ã¼zenet Ã©s az idÅ‘bÃ©lyeg */}
        <div>
          <b style={getSenderStyle(msg.sender)}>{getSenderName(msg.sender)}:</b> {msg.text}
          {msg.image && (
            <Button
              style={{ background: 'none', border: 'none', padding: '0', cursor: 'pointer' }}
              onClick={() => handleImageClick(msg.image)}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                  handleImageClick(msg.image);
                }
              }}
            >
              <img
                src={msg.image}
                alt="sent"
                style={{
                  maxWidth: '100px',
                  maxHeight: '100px',
                  objectFit: 'contain',
                  marginTop: '40px',
                }}
              />
            </Button>
          )}
          {/* Timestamp az Ã¼zenet alatt */}
          <span style={styles.timestamp}>{formatTimestamp(msg.timestamp)}</span>
          <div style={{ display: 'flex', marginTop: '5px' }}>
  {Object.entries(
    msg.reactions
      ? Object.values(msg.reactions).reduce((acc, reaction) => {
          return {
            ...acc,
            [reaction]: (acc[reaction] || 0) + 1,
          };
        }, {})
      : {}
  )
    .slice(0, 3)
    .map(([reaction, count]) => (
      <span 
        key={reaction} 
        style={{ 
          marginRight: '2px', 
          fontSize: '12px' // Set a smaller font size for reactions
        }}
      >
        {count} {reaction}
      </span>
    ))}
  {Object.keys(msg.reactions || {}).length > 3 && (
    <span style={{ fontSize: '12px' }}>+{Object.keys(msg.reactions).length - 3}</span>
  )}
</div>

        </div>
        {/* Szemetes ikon csak a sajÃ¡t Ã¼zenetekhez */}
        {msg.sender.toLowerCase() === account.toLowerCase() && (
          <Button
            onClick={() => handleDeleteMessage(msg)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              padding: '0',
              marginLeft: '10px', // Kis tÃ©rkÃ¶z a szemetes ikon elÅ‘tt
              fontSize: styles.timestamp.fontSize, // Ugyanakkora mÃ©retÅ± lesz, mint a timestamp
              display: 'inline-flex', // Inline marad a szÃ¶veggel
              alignItems: 'center', // KÃ¶zÃ©pre igazÃ­tÃ¡s
            }}
          >
            ğŸ—‘ï¸
          </Button>
        )}
      </div>
    </div>
  ))}
</ChatBox>

   
          <div style={styles.inputRow}>
            <Flex alignItems="center" justifyContent="center">
              <Input
                type="text"
                value={isSearching ? searchQuery : newMessage}
                onChange={isSearching ? handleSearchInputChange : (e) => setNewMessage(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !isSearching && cooldown === 0) {
                    handleSend();
                  }
                }}
                style={styles.input}
                placeholder={isSearching ? 'Search in chatbox...' : (cooldown > 0 ? `Cooldown: ${cooldown}s` : 'Type a message')}
                disabled={cooldown > 0 && !isSearching}
              />
              <Button onClick={handleSend} style={styles.sendButton} disabled={cooldown > 0}>
                Send
              </Button>
              <Button onClick={() => setEmojiPickerVisible(!emojiPickerVisible)} style={styles.emojiButton}>
                ğŸ˜Š
              </Button>
              <Button 
                onClick={() => fileInputRef.current.click()} 
                style={styles.fileButton}>
                ğŸ“¤
              </Button>
              <Link to="/toplist" >
    <Button variant='gold' ml="5px" width="15px" >
    ğŸ† {/* TrÃ³fea ikon */}
    </Button>
  </Link> 
            </Flex>
            {image && (
  <div style={styles.previewContainer}> 
    <Text style={styles.previewText}>Image selected:</Text>
    <img src={image} alt="Preview" style={styles.previewImage} />
    <Button onClick={() => setImage(null)} style={styles.deleteButton}>
      âŒ 
    </Button>
  </div>
)}

          </div>
  
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            style={{ display: 'none' }}
            onChange={handleImageChange}
          />

          {emojiPickerVisible && (
            <EmojiPicker 
              setNewMessage={setNewMessage} 
              newMessage={newMessage} 
              handleSendImage={handleSendImage}  // Pass handleSendImage
            /> 
          )}

{selectedMessageId && (
  <ReactionPicker
    onReactionSelect={(emoji) => {
      handleAddReaction(selectedMessageId, emoji); // Add or remove the reaction
      setSelectedMessageId(null); // Close the picker
    }}
  />
)}

{enlargedImage && (
  <div
    style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.8)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000,
    }}
    onClick={handleModalClose}
    role="button"
    tabIndex="0"
    onKeyDown={(e) => {
      if (e.key === 'Enter' || e.key === ' ') {
        handleModalClose();
      }
    }}
  >
    <img
      src={enlargedImage}
      alt="Enlarged"
      style={{ maxWidth: '90%', maxHeight: '90%', cursor: 'pointer' }}
    />
  </div>
)}

    </Container>
          ) : (
            <ConnectWalletButton />
          )}
                     </>

  );
};

export default Profile;
